/* src/App.css */

body {
  font-family: "Arial", sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.cart {
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.cart h2 {
  border-bottom: 2px solid #ccc;
  padding-bottom: 10px;
}

.cart ul {
  list-style-type: none;
  padding: 0;
}

.cart li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #eee;
}

.cart li:last-child {
  border-bottom: none;
}

.cart img {
  width: 100px;
  height: auto;
  border-radius: 5px;
  margin-right: 20px;
}

.cart div {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.cart div h3 {
  margin: 0 0 10px;
}

.cart div p {
  margin: 0;
  color: #777;
}

.cart div > div {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.cart button {
  background-color: #ff6f61;
  border: none;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cart button:hover {
  background-color: #ff4c3b;
}

.cart button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

.cart span {
  margin: 0 10px;
  font-size: 1.2em;
}

.cart p {
  margin: 10px 0 0;
  font-size: 1.1em;
  font-weight: bold;
}

.cart-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-top: 2px solid #ccc;
  margin-top: 20px;
}

.cart-total p {
  margin: 0;
  font-size: 1.2em;
}

.cart-total .total-amount {
  font-size: 1.5em;
  color: #ff6f61;
  font-weight: bold;
}
